import React from 'react';
import { Link } from 'gatsby';

// Components
import Layout from '../components/Layout/Layout';

function ErrorPage() {
	return (
		<Layout title="صفحه یافت نشد">
			<p style={{ fontWeight: 'bold', fontSize: '1.5em' }} className="text-center pt-5 m-0 mb-1">
				صفحه مورد نظر یافت نشد
			</p>
			<div className="d-flex justify-content-center pb-5">
				<Link style={{ fontWeight: 'bold', fontSize: '1.2em' }} className="pr-2" to="/">
					صفحه اصلی
				</Link>
				|
				<Link style={{ fontWeight: 'bold', fontSize: '1.2em' }} className="pl-2" to="/shop">
					فروشگاه
				</Link>
			</div>
		</Layout>
	);
}

export default ErrorPage;
